.carousel {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 415px;
  max-width: 1240px;
  margin: auto;
}

.carousel p {
  position: absolute;
  color: white;
  font-size: 18px;
  font-weight: 500;
  z-index: 1;
  bottom: 0;
}

.carousel_image {
  border-radius: 25px;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.right_vector {
  position: absolute;
  cursor: pointer;
  z-index: 1;
  right: 20px;
}

.left_vector {
  position: absolute;
  cursor: pointer;
  z-index: 1;
  left: 20px;
}

.inactive {
  opacity: 0.7;
  transition: 1s ease;
}

.active {
  height: 100%;
  width: 100%;
  opacity: 1;
  transition: 1s ease;
}

.noPictures {
  color: #FF6060;
  font-size: 36px;
  font-weight: 500;
}

@media all and (max-width: 900px) {
  .carousel {
    height: 255px;
    margin-top: 20px;
  }

  .carousel p {
    display: none;
  }

  .carousel_image {
    border-radius: 10px;
  }

  .right_vector {
    height: 20px;
    width: 11px;
    right: 10px;
  }

  .left_vector {
    height: 20px;
    width: 11px;
    left: 10px;
  }
}