.mainWrapperLogement {
    margin-left: 30px;
    margin-right: 30px;
}

.bottomCarousel h1 {
    color: #FF6060;
    font-size: 36px;
    font-weight: 300;
    margin: 0;
}

.bottomCarousel h2 {
    color: #FF6060;
    font-size: 18px;
    font-weight: 300;
    margin-top: 5px;
    margin-bottom: 25px
}

.bottomCarousel_OutCollapseInfos {
    display: flex;
    justify-content: space-between;
    max-width: 1240px;
    margin: 40px auto;
}

.bottomCarousel_OutCollapseInfos_titlesAndTags {
    display: flex;
    flex-direction: column;
}

.bottomCarousel_OutCollapseInfos_titlesAndTags_tags {
    display: flex;
    flex-wrap: wrap;
}

.bottomCarousel_OutCollapseInfos_titlesAndTags_tags_tag {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 7px 40px 7px 40px;
    margin-right: 10px;
    margin-bottom: 8px;
    width: auto;
    height: auto;
    background-color: #FF6060;
    color: white;
    font-weight: 300;
    font-size: 14px;
    border-radius: 20px;
}

.bottomCarousel_collapses {
    display: flex;
    justify-content: space-between;
    max-width: 1240px;
    margin: 0px auto;
}

.bottomCarousel_collapses_collapse {
    width: 48%;
    list-style-type: none;
}

.bottomCarousel_OutCollapseInfos_host {
    display: flex;
    flex-direction: column;
}

.bottomCarousel_OutCollapseInfos_host p {
    color: #FF6060;
    width: 65px;
    margin-right: 20px;
    text-align: end;
    font-weight: 300;
}

.bottomCarousel_OutCollapseInfos_host_infos {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.bottomCarousel_OutCollapseInfos_host_infos_picture {
    border-radius: 100px;
    height: 64px;
    width: 64px;
}

.bottomCarousel_OutCollapseInfos_host_rating {
    align-items: center;
    display: flex;
    justify-content: flex-end;
}

@media all and (max-width: 768px) {

    .bottomCarousel_OutCollapseInfos {
        flex-direction: column;
        margin-bottom: 0;
        margin-top: 10px;
    }

    .bottomCarousel_collapses {
        flex-direction: column;
    }

    .bottomCarousel_collapses_collapse {
        width: 100%;
    }

    .bottomCarousel h1 {
        font-size: 18px
    }

    .bottomCarousel h2 {
        font-size: 14px;
        margin-bottom: 10px;
    }

    .bottomCarousel_OutCollapseInfos_titlesAndTags_tags_tag {
        font-size: 10px;
        width: auto;
        height: auto;
    }

    .bottomCarousel_OutCollapseInfos_host {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        width: 100%;
    }

    .bottomCarousel_OutCollapseInfos_host_infos_picture {
        height: 32px;
        width: 32px;
    }

    .bottomCarousel_OutCollapseInfos_host p {
        font-size: 12px;
    }

}