.mainWrapperApropos {
    margin: 0 100px;
}

.bannerApropos {
    position: relative;
    height: 223px;
    margin-bottom: 30px;
}

.collapseApropos {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-right: 140px;
    margin-left: 140px;
}

@media all and (max-width: 900px) {
    
    .mainWrapperApropos {
        margin: 0;
    }

    .collapseApropos {
        margin-right: 35px;
        margin-left: 35px;
    }
}