.collapse_open {
  color: #FF6060;
  background-color: #F7F7F7;
  margin: 0px auto;
  margin-bottom: 30px;
  max-width: 1040px;
  border-radius: 5px;
}

.collapse_open p {
  padding: 35px 17px 17px 17px;
  margin: 0;
  font-size: 24px;
  font-weight: 300;
  margin-bottom: 32px;
}

.collapse_open img {
  transform: rotate(180deg);
}

.collapse_closed {
  width: 100%;
  max-width: 1040px;
  margin-bottom: 30px;
}

.collapse_button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #FF6060;
  color: white;
  width: 100%;
  height: 47px;
  font-size: 24px;
  padding: 0 30px 0 17px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

@media all and (max-width: 900px) {

  .collapse_button {
    font-size: 12px;
    padding-left: 10px;
    height: 30px;
  }

  .collapse_closed {
    margin-bottom: 20px;
  }

  .collapse_closed img {
    width: 15px;
  }

  .collapse_open {
    margin-bottom: 20px;

  }

  .collapse_open img {
    width: 15px;
  }

  .collapse_open p {
    font-size: 12px;
    padding: 15px 10px 20px 10px;
  }
}